/**
 *  axios
 */
import axios from 'axios'
import { ElMessage } from 'element-plus'

import store from '@/store'
import router from '@/router'
import Config from '@/config'
import autoJump from '@/lin/util/auto-jump'
import ErrorCode from '@/config/error-code'
import { getToken, saveAccessToken, saveTokens } from '@/lin/util/token'

const config = {
  baseURL: Config.baseURL || '',
  timeout: 300 * 1000, // 
  crossDomain: true,
  // withCredentials: true, // Check cross-site Access-Control
  // http
  // return truenullundefinedpromiseresolved,rejected
  validateStatus(status) {
    return status >= 200 && status < 510
  },
}

/**
 * refresh
 * @param { number } code 
 */
function refreshTokenException(code) {
  const codes = [10100, 10042, 10050, 10052, 10012]
  return codes.includes(code)
}

// 
const _axios = axios.create(config)

_axios.interceptors.request.use(
  originConfig => {
    //  API 
    autoJump(router)

    const reqConfig = { ...originConfig }

    // step1: 
    if (!reqConfig.url) {
      console.error('request need url')
    }

    reqConfig.method = reqConfig.method.toLowerCase() // 

    // 
    if (reqConfig.method === 'get') {
      if (!reqConfig.params) {
        reqConfig.params = reqConfig.data || {}
      }
    } else if (reqConfig.method === 'post') {
      if (!reqConfig.data) {
        reqConfig.data = reqConfig.params || {}
      }

      // ,  formData 
      let hasFile = false
      Object.keys(reqConfig.data).forEach(key => {
        if (typeof reqConfig.data[key] === 'object') {
          const item = reqConfig.data[key]
          if (item instanceof FileList || item instanceof File || item instanceof Blob) {
            hasFile = true
          }
        }
      })

      //  FormData 
      if (hasFile) {
        const formData = new FormData()
        Object.keys(reqConfig.data).forEach(key => {
          formData.append(key, reqConfig.data[key])
        })
        reqConfig.data = formData
      }
    }

    // step2: permission 
    if (reqConfig.url === 'cms/user/refresh') {
      const refreshToken = getToken('refresh_token')
      if (refreshToken) {
        reqConfig.headers.Authorization = refreshToken
      }
    } else {
      const accessToken = getToken('access_token')
      if (accessToken) {
        reqConfig.headers.Authorization = accessToken
      }
    }

    return reqConfig
  },
  error => Promise.reject(error),
)

const gotoLogin = (message) => {
  ElMessage.error(message)
  setTimeout(() => {
    store.dispatch('loginOut')
    const { origin } = window.location
    window.location.href = origin
  }, 1500)
}

_axios.interceptors.response.use(
  async res => {
    if (res.status.toString().charAt(0) === '2') {
      return res.data
    }

    const { code, message } = res.data

    return new Promise(async (resolve, reject) => {
      let tipMessage = ''
      const { url } = res.config

      // refresh_token 
      if (refreshTokenException(code)) {
        gotoLogin(message)
        return reject(null)
      }
      // assessToken
      if (code === 10000) {
        const cache = {}
        if (cache.url !== url) {
          cache.url = url
          var refresh_token = getToken('refresh_token');
          if (!refresh_token) {
            gotoLogin('')
            return reject(null)
          }
          const refreshResult = await _axios({
            method: 'get',
            url: 'cms/user/refresh',
            headers: {
              Authorization: refresh_token
            },
          })
          saveTokens(refreshResult.access_token, refreshResult.refresh_token)
          // 
          const result = await _axios(res.config)
          return resolve(result)
        }
      }

      // 
      //  handleError: true try catch 
      if (res.config.handleError) {
        return reject(res)
      }

      // 
      //  showBackend: true, 
      if (Config.useFrontEndErrorMsg && !res.config.showBackend) {
        // 
        const errorArr = Object.entries(ErrorCode).filter(v => v[0] === code.toString())
        // 
        if (errorArr.length > 0 && errorArr[0][1] !== '') {
          ;[[, tipMessage]] = errorArr
        } else {
          tipMessage = ErrorCode['777']
        }
      }

      if (typeof message === 'string') {
        tipMessage = message
      }
      if (Object.prototype.toString.call(message) === '[object Object]') {
        ;[tipMessage] = Object.values(message).flat()
      }
      if (Object.prototype.toString.call(message) === '[object Array]') {
        ;[tipMessage] = message
      }
      ElMessage.error(tipMessage)
      reject(res)
    })
  },
  error => {
    if (!error.response) {
      ElMessage.error(' API ')
      console.log('error', error)
    }

    // 
    if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
      ElMessage.warning('')
    }
    return Promise.reject(error)
  },
)

// 

/**
 * @param {string} url
 * @param {object} data
 * @param {object} params
 */
export function post(url, data = {}, params = {}) {
  return _axios({
    method: 'post',
    url,
    data,
    params,
  })
}

/**
 * @param {string} url
 * @param {object} params
 */
export function get(url, params = {}) {
  return _axios({
    method: 'get',
    url,
    params,
  })
}

/**
 * @param {string} url
 * @param {object} data
 * @param {object} params
 */
export function put(url, data = {}, params = {}) {
  return _axios({
    method: 'put',
    url,
    params,
    data,
  })
}

/**
 * @param {string} url
 * @param {object} params
 */
export function _delete(url, params = {}) {
  return _axios({
    method: 'delete',
    url,
    params,
  })
}

export default _axios
