import User from 'lin/model/user';
import { mapActions } from 'vuex';
import { post, put } from 'lin/plugin/axios';
import VuePictureCropper, { cropper } from 'vue-picture-cropper';
export default {
  components: {
    VuePictureCropper
  },
  props: {
    originalImage: {
      type: String
    },
    cropVisible: {
      type: Boolean,
      default: false
    }
  },
  emits: ['switchCropVisible'],
  methods: {
    ...mapActions(['setUserAndState']),
    handleCrop() {
      // 
      const blob = cropper.getBlob();
      // 
      const file = new File([blob], 'avatar.jpg', {
        type: 'image/jpeg'
      });
      return post('/cms/file', {
        file
      }).then(res => {
        // 
        if (!Array.isArray(res) || res.length !== 1) {
          this.$message.error(', ');
          return false;
        }
        return put('/cms/user/avatar', {
          avatar: res[0].path
        }).then(putRes => {
          if (putRes.code < window.MAX_SUCCESS_CODE) {
            this.$message({
              type: 'success',
              message: ''
            });
            this.switchStatus();
            // 
            return User.getInformation();
          }
          return Promise.reject(new Error(''));
        }).then(infoRes => {
          // eslint-disable-line
          // 
          const user = infoRes;
          this.setUserAndState(user);
        });
      });
    },
    switchStatus() {
      this.$emit('switchCropVisible', false);
    }
  }
};